$color1:#f2f2f3;

.clock-container1{
    width: 250px;
    height: 250px;
     margin-left: auto;
     margin-right: auto;
    border: none;
     margin-top: 18px;
}
.clock {
    display: flex;
    width: 250px;
    height: 250px;
    justify-content: center;
    align-items: center;
    background: url(http://samuel-garcia.site/img/clock-sam.png);
    background-size: cover;
    border: 4px solid #545454;
    background-color: #c0c0c0;
    border-radius: 50%;
    box-shadow: -4px -4px 10px rgba(67, 67, 67, 0.5), inset 4px 4px 10px rgba(0, 0, 0, 0.5), inset -4px -4px 10px rgba(67, 67, 67, 0.3), 4px 4px 10px rgba(0, 0, 0, 0.3);
    height: 100%;
    text-align: center;


}

.clock:before {
    content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    background: #747474;
    border-radius: 50%;
    z-index: 999;
}

.clock .hour,
.clock .min,
.clock .sec {
    position: absolute;
}

.clock .hour,
.hr {
    width: 120px;
    height: 120px;
}

.clock .min,
.mn {
    width: 120px;
    height: 150px;
}

.clock .sec,
.sc {
    width: 190px;
    height: 190px;
}

.hr,
.mn,
.sc {
    display: flex;
    justify-content: center;
    /*align-items: center;*/
    position: absolute;
    border-radius: 50%;
    height: 100%;
}

.hr:before {
    content: "";
    position: absolute;
    width: 8px;
    height: 60px;
    background: #504646;
    z-index: 9;
    border-radius: 6px 6px 0 0;
}

.mn:before {
    content: "";
    position: absolute;
    width: 4px;
    height: 80px;
    background: #8b8b8b;
    z-index: 10;
    border-radius: 6px 6px 0 0;
}

.sc:before {
    content: "";
    position: absolute;
    width: 2px;
    height: 100px;
    background: #e5e5e5;
    z-index: 11;
    border-radius: 6px 6px 0 0;
}