/*@import url('https://fonts.googleapis.com/css2?family=Gochi+Hand&display=swap');*/
@import '../../constant.scss';
.Todo {
    font-size: 15px;
    letter-spacing: 0.1em;
    display: flex;
    justify-content: space-between;
}

form {
    display: inline-block;
}

.addBtn {
    width: 40px;
    height: 40px;
    color:#a89f9f;
  font-size: 10px;
    border: solid 2px #ccc3c3;
    border-radius: 50%;
    background-color: #f3ecec;
margin-left: 10px;
}

.addBtn:hover {
    cursor: pointer;
    box-shadow: 1px 1px 2px 0;
}

input:focus {
    outline: none;
}

select {
    padding: 10px;
    border-right: none;
    border-left: none;
    border-top: 1px solid;
    border-bottom: 1px solid;
    outline: none;
    >option {
        font-size: 12px;
    }
}

.addBtn:focus {
    outline: none;
}

.todos-container1 {
    margin-top: 10px;

    border: none;
    color: $text;
    overflow-y: auto;
    width: 500px;
    height: 190px;
    font-size: 20px;
  //  opacity: $opacity;
}

.todos-container-item {
    display: flex;
    p {
        word-wrap: break-word;
        width: 400px;
    }
}

.filter-container {
    width: 100px;
    padding: 10px;
    border-left: 1px solid #b5b5b5;
    border-radius: 15%;
    text-align: left;
    height: 150px;

}

.category-button {
    display: block;
    border: none;
    background-color: $bg1;
   opacity:$opacity;
 color: $text;
 height: 20px;


}

