    // margin-bottom: 15px;
    // width: 40%;
//}

.categories-container {
    background-color: $bg1;
    color: $text;
    border: solid 1px #38707e;
    font-size: 15px;
    margin-left: 10px;
}

.inputToDo2 {
    background-color: $bg1;
    outline: none;
    border: none;
    border-bottom: 1px solid #ffff;

    &::placeholder {
    opacity: 0.4;
        color: $text;
        font-size: 13px;

    }
}

.btn-delete {
    color: rgb(233, 215, 215);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    background-color: #948e8e;
    font-size: 12px;
    border: none
}